import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { ScanPropertyPage } from '../scan-property/scan-property';
declare var google;


@IonicPage()
@Component({
  selector: 'page-map-view',
  templateUrl: 'map-view.html',
})
export class MapViewPage {
  currentLocation: any = '';
  @ViewChild('map') mapElement: ElementRef;
  map: any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private geolocation: Geolocation) {
  }

  ionViewDidLoad() {
    this.mapInit();


      var current = this;
      setInterval(function() {
        current.geolocation.getCurrentPosition()
          .then((location) => {
            console.log(location);
    
            var lat = location.coords.latitude;
            var lng = location.coords.longitude;
            var position = { lat: lat, lng: lng };
            current.getCurrentLocation(lat, lng);
          });
      }, 2000);
  }


  scanProperty() {
    this.navCtrl.push(ScanPropertyPage)
  }


  mapInit() {

    this.geolocation.getCurrentPosition()
      .then((location) => {
        console.log(location);

        var lat = location.coords.latitude;
        var lng = location.coords.longitude;
        var position = { lat: lat, lng: lng };


        var map = new google.maps.Map(document.getElementById('maps'), {
          center: position,
          zoom: 5
        });

        


        map = new google.maps.Map(document.getElementById('maps'), {
          center: position,
          zoom: 15
        });
        var infowindow = new google.maps.InfoWindow();
        var service = new google.maps.places.PlacesService(map);
        service.nearbySearch({
          location: position,
          radius: 500,
          type: ['store']
        }, callback);


        function callback(results, status) {
          if (status === google.maps.places.PlacesServiceStatus.OK) {
            for (var i = 0; i < results.length; i++) {
              createMarker(results[i]);
            }
          }
        }

        function createMarker(place) {
          var placeLoc = place.geometry.location;
          var marker = new google.maps.Marker({
            map: map,
            position: place.geometry.location
          });

          google.maps.event.addListener(marker, 'click', function () {
            infowindow.setContent(place.name);
            infowindow.open(map, this);
          });
        }



      })
      .catch((error) => {
        console.log('Error getting location', error);
      })


  }




  getCurrentLocation(lat, lng) {
    var _this = this;
    var geocoder = new google.maps.Geocoder();
    var latLng = new google.maps.LatLng(lat, lng);

    if (geocoder) {
      geocoder.geocode({ 'latLng': latLng }, function (results, status) {
        if (status == google.maps.GeocoderStatus.OK) {
          console.log(results[0].formatted_address);
          _this.currentLocation = results[0].formatted_address;
        }
        else {

          console.log("Geocoding failed: " + status);
        }
      }); //geocoder.geocode()
    }



    var position = { lat: lat, lng: lng };

    var map = new google.maps.Map(document.getElementById('map'), {
      zoom: 4,
      center: position
    });

    var marker = new google.maps.Marker({
      position: position,
      map: map,
      title: 'Hello World!',
      icon: "data:image/jpeg;base64,"
    });

  }


}



